<template>
    <!-- –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è -->
    <div class="auth">
        <div class="title">
            <!-- –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è - –ó–∞–≥–æ–ª–æ–≤–æ–∫ -->
